'use client';

import { useEffect, useRef, useState } from 'react';
import { CommercePlatform } from './commercePlatform';
import { Footer } from './footer';
import { Hero } from './hero';
import { LandingHeader } from './header';
import { SectionContext } from './landingSectionContext';
import { MercatoTools } from './mercatoTools';
import { Pricing } from './pricing';
import { ScrollGate } from './ScrollGate';
import { Testimonials } from './testimonials';

export function LandingPageContent() {
  const pricingSectionRef = useRef<HTMLDivElement>(null);
  const [isInPricingSection, setIsInPricingSection] = useState(false);

  useEffect(() => {
    const el = pricingSectionRef.current;
    if (!el) return;
    const observer = new IntersectionObserver(
      (entries) => {
        for (const entry of entries) {
          setIsInPricingSection(entry.isIntersecting);
        }
      },
      { root: null, rootMargin: '0px', threshold: 0 }
    );
    observer.observe(el);
    return () => observer.disconnect();
  }, []);

  return (
    <SectionContext.Provider value={{ isInPricingSection }}>
      <div className="min-h-screen scroll-smooth">
        {/* Header at page level so sticky works when in Pricing/Testimonials/Footer */}
        <LandingHeader />
        <Hero />
        <ScrollGate>
          <CommercePlatform />
          <MercatoTools />
        </ScrollGate>
        <div
          ref={pricingSectionRef}
          className="relative z-20 -mt-[20vh] overflow-visible rounded-t-3xl bg-[#020205]"
        >
          <Pricing />
          <Testimonials />
          <Footer />
        </div>
      </div>
    </SectionContext.Provider>
  );
}
