'use client';

import React, { useEffect, useRef, useState, createContext, useContext } from 'react';

type ScrollGateContextValue = {
  progress: number;
  allowInnerScroll: boolean;
  scrollContainerRef: React.RefObject<HTMLDivElement | null>;
};
const ScrollGateContext = createContext<ScrollGateContextValue>({
  progress: 0,
  allowInnerScroll: true,
  scrollContainerRef: { current: null },
});
export function useScrollGate() {
  return useContext(ScrollGateContext);
}

/**
 * Single smooth scroll: gate content scrolls via transform driven by window scroll.
 * A spacer ensures you must scroll the gate to bottom (sagad) before Pricing layer moves.
 * Parallax and progress stay; no separate inner scroll container.
 */
export function ScrollGate({ children }: { children: React.ReactNode }) {
  const viewportRef = useRef<HTMLDivElement>(null);
  const contentRef = useRef<HTMLDivElement>(null);
  const sectionRef = useRef<HTMLDivElement>(null);
  const [innerProgress, setInnerProgress] = useState(0);
  const [contentHeight, setContentHeight] = useState(0);
  const [spacerHeight, setSpacerHeight] = useState(0);
  const [gateStartY, setGateStartY] = useState(0);

  const viewportHeight = typeof window !== 'undefined' ? window.innerHeight - 5 * 16 : 600; // 5rem

  // Measure content height and compute spacer + gate start
  useEffect(() => {
    const measure = () => {
      const content = contentRef.current;
      const section = sectionRef.current;
      if (!content || !section) return;
      const ch = content.offsetHeight;
      setContentHeight(ch);
      const sh = Math.max(0, ch - viewportHeight);
      setSpacerHeight(sh);
      const rect = section.getBoundingClientRect();
      const scrollY = typeof window !== 'undefined' ? window.scrollY : 0;
      // When sticky viewport sticks (top-20): start mapping scroll to gate progress
      setGateStartY(scrollY + rect.top - 20);
    };
    measure();
    const ro = contentRef.current ? new ResizeObserver(measure) : null;
    if (contentRef.current) ro?.observe(contentRef.current);
    window.addEventListener('resize', measure);
    return () => {
      ro?.disconnect();
      window.removeEventListener('resize', measure);
    };
  }, [viewportHeight, children]);

  // Single scroll: map window scroll to gate progress and content translateY
  useEffect(() => {
    const onScroll = () => {
      const scrollY = typeof window !== 'undefined' ? window.scrollY : 0;
      if (spacerHeight <= 0) {
        setInnerProgress(1);
        return;
      }
      const start = gateStartY;
      const end = start + spacerHeight;
      const progress = Math.min(1, Math.max(0, (scrollY - start) / spacerHeight));
      setInnerProgress(progress);
    };
    onScroll();
    window.addEventListener('scroll', onScroll, { passive: true });
    return () => window.removeEventListener('scroll', onScroll);
  }, [gateStartY, spacerHeight]);

  const scrollOffset = Math.min(contentHeight - viewportHeight, innerProgress * Math.max(0, contentHeight - viewportHeight));

  return (
    <ScrollGateContext.Provider value={{ progress: innerProgress, allowInnerScroll: true, scrollContainerRef: viewportRef }}>
      <div ref={sectionRef} className="relative z-10 -mt-[40vh] w-full">
        {/* Sticky viewport: stays in place while spacer scrolls; content inside moves via translateY */}
        <div
          ref={viewportRef}
          className="scrollbar-hide sticky top-20 z-10 w-full overflow-hidden rounded-t-3xl bg-[#092727]"
          style={{ height: viewportHeight }}
        >
          <div
            ref={contentRef}
            className="w-full will-change-transform"
            style={{ transform: `translateY(-${scrollOffset}px)` }}
          >
            {children}
          </div>
        </div>
        {/* Spacer: must scroll this much (sagad) before Pricing layer gets scroll */}
        <div style={{ height: spacerHeight }} aria-hidden />
      </div>
    </ScrollGateContext.Provider>
  );
}
